body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.form {
    text-align: center;
    background-color: #f5f5f5;
    min-height: 100vh;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.stepButton{
    margin-bottom: 10px;
}
.stepButton>button{
    background-color: #ffffff;
    border: none;
    width: 7vh;
    height: 7vh;
    margin: 0 6px;
}
button>img{
    height: 7vh;
}
img[alt="leftArrow"]{
    margin-right: 10px;
}
img[alt="rightArrow"]{
    margin-left: 10px;
}
button>span{
    font-size: 1.2rem;
}
.stepArrow{
    display: flex;
}
.stepArrow>button {
    margin:5px;
    color: #ff9800;
    box-shadow: 0 1px 2px rgba(0,0,0,0.2);
    height: 7vh;
    background-color: #ffffff;
    border: none;
    border-radius: 2px;
    display: flex;
    align-items:center;
}
.activ{
    color: #ff9800;
}
*{
    box-sizing: border-box;
}
.first{
    text-align: center;
    background-color: #f5f5f5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.first>input{
    border: none;
    border-radius: 2px;
    padding: 0 10px;
    margin: 8px 0;
    width: 20vw;
    height: 35px;
}
.error{
    height: 50px;
    color: red;
}
@media (max-width: 1081px) {
    .first>input{
        width: 80vw;
    }
}
.second{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}
.nullSelect{
    margin: 10px;
    height: 35px;
}
select{
    margin: 10px;
    background-color: #ffffff;
    width: 20vw;
    height: 35px;
    border: none;
    padding-left: 5px;
}
option{
    color: black;
}
@media (max-width: 1081px) {
    select{
        width: 80vw;
    }
}

.socialNetwork{
    display: flex;
    flex-direction: column;
    width: 25vw;
    justify-content: flex-start;
}
.socialNetwork> div{
    margin: 10px;
    height: 30px;
    display: flex;
    flex-direction: row;
}
.socialNetwork .checkbox{
    width: 40%;
    display: flex;
    justify-content: flex-start;
}
.link{
    padding-left: 5px;
    width: 60%;
    height: 100%;
}
.noneInput{
    width: 55%;
}
@media (max-width: 1081px) {
    .socialNetwork{
        width: 80vw;
    }
}
.info{
    width: 60%;
}
.pictures{
    padding: 10px;
    display: inline-flex;
    flex-wrap: wrap;
    align-items: center;
    justify-content: center;
}
input[name="animal"]{
    display:none;
}
.pictures>div>label{
    cursor: pointer;
    margin: 8px;
}
input[id="cat1"]:checked ~ .pictures div label[for="cat1"] img,
input[id="cat2"]:checked ~ .pictures div label[for="cat2"] img,
input[id="cat3"]:checked ~ .pictures div label[for="cat3"] img,
input[id="dog4"]:checked ~ .pictures div label[for="dog4"] img{
    border:4px solid #ff9800;
    border-radius: 4px;
}
.valid{
    color:red;
    height: 50px;
    justify-content: flex-start;
}
label{
    width: 180px;
    height: 180px;
}
label>img{
    width: 150px;
    height: 150px;
}
.summary{
    min-height: 100vh;
    background-color: #f5f5f5;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    box-shadow: 0 1px 2px rgba(0,0,0,.2);
}
.card{
    background-color: white;
    display: flex;
    justify-content:space-between ;
    width: 500px;
}
.info{
    padding: 10px;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
}
.mainInfo>h1,
.mainInfo>p{
    margin: 0;
}
.mainInfo>p,
.info>p,
.socialLink{
    color: #666666;
}
.socialName{
    color: blue;
}
.picture{
    margin: auto 10px;
    width: 200px;
    height: 195px;
    overflow: hidden;
}
.picture img{
    width: 100%;
    height: 100%;
}
.reset{
    margin-top: 20px;
    width: 160px;
    height: 30px;
    font-size: 1.2rem;
    border: none;
    background-color: #ff9800;
    color: #ffffff;
}
